<template>
  <div class="wrap">
    <app-header title="新增核销子账号" showBack="true" />
    <div class="main">
      <mt-field label="账号" type="text" placeholder="请输入账号" v-model="account"></mt-field>
      <mt-field label="密码" type="password" placeholder="请输入密码" v-model="password"></mt-field>
      <mt-field label="姓名" type="text" placeholder="请输入姓名" v-model="name"></mt-field>
      <mt-radio
        title="分配权限"
        v-model="label"
        :options="labels">
      </mt-radio>
      <mt-button @click="submit" type="primary" size="large" style="margin: 2rem auto;">确认</mt-button>
    </div>
  </div>
</template>

<script>
import AppHeader from '../../components/header/header'
import {createAccount, getCouponLabels} from '../../api/maintain'
export default {
  name: 'Dealer-Create-Account',
  data () {
    return {
      account: '',
      password: '',
      name: '',
      label: '',
      labels: []
    }
  },
  created () {
    this.getLabels()
  },
  methods: {
    getLabels () {
      getCouponLabels()
        .then(data => {
          if (data.state) {
            let datas = data.data.rank
            for (let i in datas) {
              // let _o = {
              //   label: datas[i].category_cn,
              //   value: datas[i].label_id
              // }
              // this.labels.push(_o)
              if (datas[i].labels) {
                for (let j in datas[i].labels) {
                  let _o = {
                    label: datas[i].labels[j].label_cn,
                    value: datas[i].labels[j].label_id
                  }
                  this.labels.push(_o)
                }
              }
            }
          }
        })
    },
    submit () {
      createAccount(this.account, this.password, this.label, this.name)
        .then(data => {
          if (data.state) {
            this.$toast(data.errormsg || '添加成功')
            this.$router.back()
          } else {
            this.$toast(data.errormsg)
          }
        })
        .catch(err => {
          console.log(err)
          this.$toast('网络错误，请重试')
        })
    }
  },
  components: {AppHeader}
}
</script>

<style lang="less" scoped>
.wrap{
  background: #e5e5e5;
  height: 100%;
  padding: 55px 0;
  overflow-x: hidden;
  overflow-y: scroll;
}
</style>



